.example-map {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.example-map__container {
  -ms-flex: 1 1;
      flex: 1 1;
}

.example-map__actions {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  margin-bottom: 16px;
}

.example-map__action {
  background: transparent;
  outline: none;
  cursor: pointer;
  border: 1px solid #fff;
  border-radius: 4px;
  margin-right: 8px;
  color: #fff;
  padding: 6px 12px;
  font-size: 14px;
  -webkit-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
}

.example-map__action:hover {
  background: rgba(255, 255, 255, 0.25);
}



